import React, {Component} from 'react';
import {List} from 'semantic-ui-react';

class IdeaList extends Component {
    render() {
        var count=0;
        var list = this.props.list.map(function(elem) {
            return (
                <List.Item key={count++} >{elem}</List.Item>
            )
        });
        return (
            <div >
                <List size='large'>
                    {list}
                </List>
            </div>
        );
    }
}

export default IdeaList;



