import React, { Component } from 'react';
import AddIdeas from './AddIdeas';
import IdeaList from './IdeaList';
import { Grid } from 'semantic-ui-react'
import IdeaAPI from './ideaAPI';
import '../App.css';

class Main extends Component {
    constructor(){
        super();
        this.state = {
            list: IdeaAPI.all()
        }
    }
    updateList(text){
        var updatedList = this.state.list;
        updatedList.push(text);
        this.setState({ list: updatedList})
    }

  render() {
    return (
        <Grid celled>
  <Grid.Row>
    <Grid.Column width={5}>
    </Grid.Column>
    <Grid.Column width={6}>
        <h1> Ideas </h1>
        <AddIdeas updatelist={this.updateList.bind(this)} />
        <IdeaList  list={this.state.list} />
    </Grid.Column>
    <Grid.Column width={5}>

    </Grid.Column>
  </Grid.Row>
</Grid>
    );
  }
}

export default Main;



