import React, { Component } from 'react';
import { Input } from 'semantic-ui-react';
import { Button, Icon } from 'semantic-ui-react'



class AddIdeas extends Component {
    
    addidea(e){
        e.preventDefault();
        var input = e.target.querySelector('input');
        var newidea = input.value;
        input.value = '';
        this.props.updatelist(newidea);
    }
  render() {
    return (
      <div >
          <form onSubmit={this.addidea.bind(this)}>
          <Input type='text' focus placeholder='Write your idea heare...' />
          <Button type='submit' animated>
          <Button.Content visible>Submit ideas</Button.Content>
          <Button.Content hidden>
            <Icon name='idea' />
          </Button.Content>
        </Button>
          </form>
      </div>
    );
  }
}

export default AddIdeas;



